.pv-container {
  overflow: hidden;
}
.pv-container .pv-block {
  will-change: transform;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.pv-container .pv-block video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.pv-container .audio-icon {
  height: 30px;
  width: 30px;
  position: absolute;
  top: 10px;
  left: 10px;
}
.pv-container .audio-icon span {
  display: block;
  width: 8px;
  height: 8px;
  background: #fff;
  margin: 11px 0 0 2px;
}
.pv-container .audio-icon span:after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent #fff transparent transparent;
  border-width: 10px 14px 10px 15px;
  left: -13px;
  top: 5px;
}
.pv-container .audio-icon span:before {
  transform: rotate(45deg);
  border-radius: 0 50px 0 0;
  content: '';
  position: absolute;
  width: 13px;
  height: 13px;
  border-style: double;
  border-color: #fff;
  border-width: 7px 7px 0 0;
  left: 18px;
  top: 9px;
  transition: all 0.2s ease-out;
}
.pv-container .audio-icon:hover span:before {
  transform: scale(0.8) translate(-3px, 0) rotate(42deg);
}
.pv-container .audio-icon.mute span:before {
  transform: scale(0.5) translate(-15px, 0) rotate(36deg);
  opacity: 0;
}
